/* 흐르는 text css */




.animated-title {
  color:#000;
  font-size:70px; 
  font-family: 'Playfair Display', serif;
  font-weight: 500; 
  position: relative; 
  width: 100%;
  max-width:100%; 
  height: auto; 
  padding:100px 0;
  margin-top:-100px;
  margin-bottom: 20px;
  overflow-x: 
  hidden; 
  overflow-y: hidden; }
.animated-title .track {position: absolute; white-space: nowrap;will-change: transform;-webkit-animation: marquee 60s linear infinite;animation: marquee 60s linear infinite; }
@-webkit-keyframes marquee {
  from { -webkit-transform: translateX(0); transform: translateX(0); }
  to { -webkit-transform: translateX(-50%); transform: translateX(-50%); }
}
@keyframes marquee {
  from { -webkit-transform: translateX(0); transform: translateX(0); }
  to { -webkit-transform: translateX(-50%); transform: translateX(-50%); }
}
@media (hover: hover) and (min-width: 700px){
.animated-title .content {-webkit-transform: translateY(calc(100% - 8rem)); transform: translateY(calc(100% - 8rem));}
    }
.menu {
  
  padding: 0 20px;
  border: 3px solid #000000;
  border-radius: 4em;
  /* border-bottom: solid 1px #e8e8e8; */
  overflow: auto;
  box-shadow: 0 0 30px #f3f1f1;
  background-color: #f7f6f2;
  font-family: 'Spoqa Han Sans Neo', 'sans-serif';
}

.menu__logo {
  width: 150px;
  float: left;

}

.menu__logo a {
  display: inline-block;
  font-size: 20px;
  padding: 19px 20px;
  color: #000000;
}

.menu__logo a:HOVER {
  font-family: 'Playfair Display', serif;
}

.menu__container .ant-menu-item {
  padding: 0px 5px;
  background-color: #f7f6f2;


}

.menu__container .ant-menu-submenu-title {
  padding: 10px 20px;
  color: #000000;
  background-color: #f7f6f2;

}

/* .menu__container .ant-menu-submenu-title:HOVER {

  color: #000000;
  font-family: 'Playfair Display', serif;

  border-bottom: 1px solid #000000;
  
} */

.menu__container .ant-menu-item a,
.menu__container .ant-menu-submenu-title a {
  padding: 10px 15px;
  color: #000000;

}



.menu__container .ant-menu-horizontal {
  font-size: 20px;
  border-bottom: none;
}



.menu__container .menu_left {
  float: left;
}

.menu__container .menu_right {
  float: right;
}

.menu__mobile-button {
  border: none;
  cursor: pointer;
  float: right;
 
  padding: 6px;
  margin-top: 8px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: #000000;
  display: none !important; /* use of important to overwrite ant-btn */

}
.menu__mobile-button:HOVER{
 
  background: #ffffff;
  border:3px solid #000000;

}


.menu_drawer .ant-drawer-body {
  padding: 0 !important;
  
}




/* align header of Drawer with header of page */
.menu_drawer .ant-drawer-header {
  padding: 14px 24px !important;

}

@media (max-width: 767px) {
  .menu__mobile-button {
    display: inline-block !important;
  }

  .menu_left,
  .menu_right {
    display: none;
  }

  .menu__logo a {
    margin-left: -20px;
  }

  .menu__container .ant-menu-item,
  .menu__container .ant-menu-submenu-title {
    padding: 1px 20px;
  }

  .menu__logo a {
    padding: 10px 20px;

  }

  .shopping-cart a{
    font-size: 10px;
  }
}
